
"use client";

import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Users, User, CheckCircle, XCircle, Clock, FileText, Trash2, Hourglass } from "lucide-react";
import { useState, useEffect } from "react";
import { Semester, SessionStudent, Session } from "@/lib/types";
import { useAuth } from "@/context/auth-context";
import { Loader2 } from "lucide-react";
import { Separator } from "@/components/ui/separator";
import { Button } from "@/components/ui/button";
import { cn } from "@/lib/utils";
import { db } from "@/lib/firebase";
import { collection, onSnapshot } from "firebase/firestore";


const ScheduleGrid = ({ semester, teacher }: { semester: Semester | undefined, teacher: string }) => {
  const days = ["Saturday", "Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday"];
  const timeSlots = Array.from({ length: 13 }, (_, i) => `${i + 9}:00`); // 9am to 9pm
  const { user } = useAuth();

  const [localSchedule, setLocalSchedule] = useState<Record<string, Session[]>>({});

  useEffect(() => {
    if (semester?.masterSchedule?.[teacher]) {
      setLocalSchedule(structuredClone(semester.masterSchedule[teacher]));
    } else {
      setLocalSchedule({});
    }
  }, [semester, teacher]);


  if (!semester || !teacher) {
    return <div className="p-4 text-center text-muted-foreground">Please select a semester and a teacher to view the schedule.</div>;
  }

  const teacherSchedule = localSchedule;

  if (!teacherSchedule) {
    return <div className="p-4 text-center text-muted-foreground">No schedule found for this teacher in the selected semester.</div>;
  }

  const handleAttendanceChange = (day: string, sessionId: string, studentId: string, status: SessionStudent['attendance']) => {
    setLocalSchedule(prev => {
        const newSchedule = { ...prev };
        const daySchedule = newSchedule[day];
        if (!daySchedule) return prev;

        const session = daySchedule.find(s => s.id === sessionId);
        if (!session) return prev;
        
        const student = session.students.find(s => s.id === studentId);
        if (student) {
            student.attendance = status;
        }
        return newSchedule;
    });
  }

  const handleRemoveStudent = (day: string, sessionId: string, studentId: string) => {
     setLocalSchedule(prev => {
        const newSchedule = { ...prev };
        const daySchedule = newSchedule[day];
        if (!daySchedule) return prev;
        const session = daySchedule.find(s => s.id === sessionId);
        if (!session) return prev;
        
        if (user?.activeRole === 'admin' || user?.activeRole === 'high-level-dashboard') {
             session.students = session.students.filter(s => s.id !== studentId);
        } else {
            const student = session.students.find(s => s.id === studentId);
            if(student) student.pendingRemoval = true;
        }
        return newSchedule;
    });
  }

  
  const getSessionForSlot = (day: string, time: string) => {
    const daySchedule = teacherSchedule[day];
    if (!daySchedule) return null;
    
    const hour = parseInt(time.split(':')[0]);
    
    return daySchedule.find(session => {
        const sessionTimeMatch = session.time.match(/(\d+):00 (AM|PM)/);
        if (!sessionTimeMatch) { 
            const sessionStartHour24 = parseInt(session.time.split(':')[0]);
            return hour === sessionStartHour24;
        };

        let sessionStartHour = parseInt(sessionTimeMatch[1]);
        const isPM = sessionTimeMatch[2] === 'PM';

        if (isPM && sessionStartHour !== 12) {
            sessionStartHour += 12;
        }
        if (!isPM && sessionStartHour === 12) { 
            sessionStartHour = 0;
        }
        return hour === sessionStartHour;
    });
  }
  
  const getRowSpan = (session: any) => {
      return session.duration || 1;
  }

  return (
    <div className="grid grid-cols-[auto_1fr_1fr_1fr_1fr_1fr_1fr_1fr] auto-rows-fr gap-1 text-xs">
        <div className="sticky left-0 top-0 z-10 bg-background/95 backdrop-blur-sm">
             <div className="h-8 border-b"></div>
             {timeSlots.map(time => (
                <div key={time} className="h-20 flex items-center justify-center border-b px-2">{time}</div>
            ))}
        </div>
      
        {days.map(day => (
        <div key={day} className="col-span-1">
          <div className="sticky top-0 z-10 bg-background/95 backdrop-blur-sm h-8 flex items-center justify-center font-semibold border-b">{day}</div>
          <div className="grid auto-rows-[5rem] gap-1">
            {timeSlots.map((time) => {
              const session = getSessionForSlot(day, time);
              if (session) {
                 const currentHour = parseInt(time.split(':')[0]);
                 const sessionTimeMatch = session.time.match(/(\d+):00 (AM|PM)/);
                 let sessionStartHour = -1;
                 
                 if (sessionTimeMatch) {
                    sessionStartHour = parseInt(sessionTimeMatch[1]);
                    const isPM = sessionTimeMatch[2] === 'PM';
                    if (isPM && sessionStartHour !== 12) sessionStartHour += 12;
                    if (!isPM && sessionStartHour === 12) sessionStartHour = 0;
                 } else {
                    sessionStartHour = parseInt(session.time.split(':')[0]);
                 }
                
                 if (currentHour > sessionStartHour) return null;


                 return (
                    <div
                      key={`${day}-${time}`}
                      className="bg-card border rounded-lg p-2 overflow-hidden flex flex-col"
                      style={{ gridRow: `span ${getRowSpan(session)} / span ${getRowSpan(session)}`}}
                    >
                      <div className="flex justify-between items-start">
                        <p className="font-semibold text-primary">{session.specialization}</p>
                        <p className="text-muted-foreground">{session.time}</p>
                      </div>
                      <Separator className="my-1"/>
                      <div className="flex-grow overflow-y-auto pr-1">
                        {session.students && session.students.length > 0 ? (
                           <ul className="space-y-2">
                             {session.students.map((student: SessionStudent) => (
                               <li key={student.id} className={cn("flex flex-col gap-2 p-1.5 rounded-md", student.pendingRemoval && "bg-destructive/10 opacity-50")}>
                                 <div className="flex justify-between items-center">
                                    <div className="flex items-center gap-2">
                                        <User className="h-3 w-3 shrink-0" />
                                        <span className="text-xs font-medium">{student.name}</span>
                                        {student.pendingRemoval && <Hourglass className="h-3 w-3 text-destructive" />}
                                    </div>
                                    <Button size="icon" variant="ghost" className="h-5 w-5" onClick={() => handleRemoveStudent(day, session.id, student.id)}>
                                        <Trash2 className="h-3 w-3" />
                                    </Button>
                                 </div>
                                 <div className="flex items-center justify-end gap-1">
                                    <Button size="icon" variant={student.attendance === 'present' ? 'default' : 'outline'} className="h-6 w-6" onClick={() => handleAttendanceChange(day, session.id, student.id, 'present')}> <CheckCircle className="h-3 w-3"/> </Button>
                                    <Button size="icon" variant={student.attendance === 'absent' ? 'destructive' : 'outline'} className="h-6 w-6" onClick={() => handleAttendanceChange(day, session.id, student.id, 'absent')}> <XCircle className="h-3 w-3"/> </Button>
                                    <Button size="icon" variant={student.attendance === 'late' ? 'secondary' : 'outline'} className="h-6 w-6" onClick={() => handleAttendanceChange(day, session.id, student.id, 'late')}> <Clock className="h-3 w-3"/> </Button>
                                    <Button size="icon" variant={student.attendance === 'excused' ? 'secondary' : 'outline'} className="h-6 w-6" onClick={() => handleAttendanceChange(day, session.id, student.id, 'excused')}> <FileText className="h-3 w-3"/> </Button>
                                 </div>
                               </li>
                             ))}
                           </ul>
                         ) : (
                           <p className="text-xs text-muted-foreground italic mt-2">No students</p>
                         )}
                      </div>
                    </div>
                  );
              }
              
              return (
                <div key={`${day}-${time}`} className="h-20 border-b"></div>
              );
            })}
          </div>
        </div>
      ))}
    </div>
  );
};


export default function DashboardPage() {
    const { user } = useAuth();
    const [semesters, setSemesters] = useState<Semester[]>([]);
    const [loading, setLoading] = useState(true);
    const [selectedSemesterId, setSelectedSemesterId] = useState<string>("");
    const [selectedTeacher, setSelectedTeacher] = useState<string>("");

    useEffect(() => {
        setLoading(true);
        const unsub = onSnapshot(collection(db, 'semesters'), (snapshot) => {
            const allSemesters = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Semester));
            setSemesters(allSemesters);
            if (allSemesters.length > 0 && !selectedSemesterId) {
                const defaultSemester = allSemesters.find(s => s.id === 'fall-2024') || allSemesters[0];
                setSelectedSemesterId(defaultSemester.id);
            }
            setLoading(false);
        }, (error) => {
            console.error("Error fetching semesters: ", error);
            setLoading(false);
        });
        return () => unsub();
    }, []);

    const selectedSemester = semesters.find(s => s.id === selectedSemesterId);
    
    const availableTeachers = user?.activeRole === 'teacher' 
        ? selectedSemester?.teachers.filter(t => t === user.name) || []
        : selectedSemester?.teachers || [];

    useEffect(() => {
        if (selectedSemester) {
            if (user?.activeRole === 'teacher' && user.name) {
                setSelectedTeacher(user.name);
            } else if (availableTeachers.length > 0 && !selectedTeacher) {
                setSelectedTeacher(availableTeachers[0]);
            }
        }
    }, [selectedSemester, availableTeachers, user, selectedTeacher]);

    const handleSemesterChange = (semesterId: string) => {
        setSelectedSemesterId(semesterId);
        setSelectedTeacher(""); // Reset teacher so the effect can pick the default one
    };

    if (loading) {
        return (
            <div className="flex h-[calc(100vh-8rem)] items-center justify-center">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
            </div>
        );
    }
    
    return (
        <div className="space-y-4 pb-4">
            <h1 className="text-3xl font-bold font-headline">Master Schedule</h1>
            
            <Card>
                <CardHeader>
                    <CardTitle>Schedule Selector</CardTitle>
                    <div className="flex flex-col md:flex-row gap-4 pt-2">
                        <div className="flex-1">
                            <label htmlFor="semester-select" className="text-sm font-medium">Semester</label>
                            <select
                                id="semester-select"
                                value={selectedSemesterId}
                                onChange={(e) => handleSemesterChange(e.target.value)}
                                className="w-full mt-1 p-2 border rounded-md bg-background"
                                disabled={semesters.length === 0}
                            >
                                <option value="" disabled>Select a semester</option>
                                {semesters.map(s => <option key={s.id} value={s.id}>{s.name}</option>)}
                            </select>
                        </div>
                        <div className="flex-1">
                            <label htmlFor="teacher-select" className="text-sm font-medium">Teacher</label>
                            <select
                                id="teacher-select"
                                value={selectedTeacher}
                                onChange={(e) => setSelectedTeacher(e.target.value)}
                                className="w-full mt-1 p-2 border rounded-md bg-background"
                                disabled={!selectedSemester || user?.activeRole === 'teacher'}
                            >
                                 <option value="" disabled>Select a teacher</option>
                                {availableTeachers.map(t => <option key={t} value={t}>{t}</option>)}
                            </select>
                        </div>
                    </div>
                </CardHeader>
                <CardContent>
                    <div className="overflow-x-auto">
                      <ScheduleGrid semester={selectedSemester} teacher={selectedTeacher} />
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}
